.hero {
  &__container {
    width: 100%;
    display: grid;
    grid-template-columns: minmax(242px, 34%) 60.5%;
    column-gap: 5.5%;

    @include vp-1199 {
      width: 100%;
      grid-template-columns: 48% 48%;
      column-gap: 4%;
    }

    @include vp-767 {
      grid-template-columns: 1fr;
    }

    &::after {
      margin-top: -42px;

      content: "";
      display: block;
      width: 500px;
      height: 556px;
      background-image: image-set(
        url("../img/man-in-superman-t-shirt-desktop@1x.jpg") 1x,
        url("../img/man-in-superman-t-shirt-desktop@2x.jpg") 2x);

      @include vp-1199 {
        margin-top: 0;

        width: 324px;
        height: 390px;
        background-image: image-set(
          url("../img/man-in-superman-t-shirt-tablet@1x.jpg") 1x,
          url("../img/man-in-superman-t-shirt-tablet@2x.jpg") 2x);
      }

      @include vp-767 {
        display: none;
      }
    }
  }

  &__left-side-container {
    display: grid;
    grid-template-columns: 33% 67%;
    grid-template-rows: minmax(126px, min-content) minmax(190px, min-content) minmax(94px, min-content);
    row-gap: 15px;
    justify-content: end;
    align-items: end;

    @include vp-1199 {
      grid-template-columns: 35% 65%;
      grid-template-rows: minmax(90px, min-content) minmax(166px, min-content) minmax(82px, min-content);
    }

    @include vp-767 {
      grid-template-rows: minmax(87px, min-content) minmax(172px, min-content) minmax(69px, min-content);
    }
  }

  &__city {
    margin: 0;
    margin-right: 50px;
    grid-area: 1 / 2 / 2 / -1;
    text-align: end;

    color: #ffffff;
    font-family: "Alumni Sans", "Arial", sans-serif;
    font-size: 48px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    letter-spacing: 1.44px;
    text-transform: lowercase;

    @include vp-1199 {
      margin-right: 4px;

      color: #ffffff;
      font-size: 34px;
      letter-spacing: 1.02px;
    }

    @include vp-767 {
      margin-right: 3px;

      font-size: 28px;
      letter-spacing: 0.84px;
    }
  }

  &__title {
    margin: 0;
    grid-area: 2 / 2 / 3 / -1;

    color: #ffffff;
    text-align: right;
    font-family: "Alumni Sans", "Arial", sans-serif;
    font-size: 92px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    letter-spacing: 4.6px;

    -webkit-text-stroke: 2px #ffffff;
    text-transform: uppercase;

    @include vp-1199 {
      margin-top: -8px;

      font-size: 72px;
      letter-spacing: 3.6px;
      text-transform: unset;
    }
  }

  &__features-list {
    list-style: none;
    margin: 0;
    margin-bottom: 36px;
    padding: 0;
    grid-area: 2 / 1 / 3 / 2;

    color: #ffffff;
    text-align: right;
    font-family: $ff-roboto;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 150%;

    @include vp-1199 {
      margin-bottom: 22px;
    }

    @include vp-767 {
      margin-right: -9px;
      min-width: 113px;
      margin-bottom: 32px;
    }
  }

  &__btn {
    grid-area: 3 / 2 / -1 / -1;
    justify-self: end;
    align-self: end;

    min-width: 87.5%;

    @include vp-767 {
      min-width: 100%;
      grid-area: 3 / 1 / -1 / -1;
    }
  }
}
