  display: flex;
  justify-content: space-between;
}
@media (max-width: 767px) {
  .registration__form fieldset {
    flex-direction: column;
    gap: 30px;
    margin-bottom: 10px;
  }
}
.registration__form input::placeholder {
  color: #ffffff;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}
.registration__form input {
  background: transparent;
  border: none;
  outline: none;
  border-bottom: 1px solid #74819c;
  margin-bottom: -1px;
  padding: 9px 0;
  color: #ffffff;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  width: 100%;
}
@media (max-width: 1199px) {
  .registration__form input {
    padding: 6px 0;
  }
}
@media (hover: hover) {
  .registration__form input:hover:not(.focus-visible) {
    border-bottom: 1px solid #ffffff;
    margin-bottom: -1px;
  }
}
.registration__form input.focus-visible:focus {
  border: 1px solid #ffffff;
  margin: -1px;
}
.registration__form input.focus-visible:focus::placeholder {
  color: transparent;
}
.registration__form input:active {
  border: 1px solid #ffffff;
  margin: -1px;
}
.registration__form input:active::placeholder {
  color: transparent;
}
.registration__form input:disabled {
  pointer-events: none;
  opacity: 0.3;
}
.registration__form button {
  color: #ffffff;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
  letter-spacing: 0.7px;
  text-transform: uppercase;
  padding: 18px;
}

.registration-contacts-wrapper {
  display: grid;
  grid-template-columns: 50% 50%;
}
@media (max-width: 1199px) {
  .registration-contacts-wrapper {
    grid-template-columns: 100%;
  }
}

.contacts {
  padding-top: 56px;
  border-top: 1px solid #74819c;
}
@media (max-width: 1199px) {
  .contacts {
    border-top: none;
    padding-top: 60px;
    padding-bottom: 63px;
  }
}
@media (max-width: 767px) {
  .contacts {
    padding-top: 64px;
    padding-bottom: 62px;
  }
}
.contacts .container {
  padding-right: 0;
}
.contacts__container {
  margin-left: 20px;
  width: 78%;
  display: grid;
  grid-template-columns: 44% 36%;
  column-gap: 20%;
  position: relative;
}
@media (max-width: 1199px) {
  .contacts__container {
    margin-left: 88px;
    column-gap: 11.5%;
  }
}
@media (max-width: 767px) {
  .contacts__container {
    grid-template-columns: 100%;
    margin-left: 0;
    column-gap: 0;
    row-gap: 22px;
  }
}
.contacts__heading {
  color: #102654;
  font-family: "Alumni Sans", "Arial", sans-serif;
  font-size: 54px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  letter-spacing: 1.62px;
  grid-column: 1/3;
  grid-row: 1/2;
  margin: 0;
  margin-left: 16px;
  margin-bottom: 36px;
}
@media (max-width: 1199px) {
  .contacts__heading {
    font-size: 42px;
    letter-spacing: 1.26px;
    margin-left: 88px;
    margin-bottom: 33px;
  }
}
@media (max-width: 767px) {
  .contacts__heading {
    font-size: 32px;
    letter-spacing: 0.96px;
    margin-left: 0;
  }
}
.contacts__wrapper {
  display: flex;
  flex-direction: column;
  gap: 17px;
}
@media (max-width: 1199px) {
  .contacts__wrapper {
    gap: 20px;
  }
}
.contacts__wrapper--rightlined::after {
  content: "";
  display: block;
  width: 1px;
  height: 87%;
  background: #74819c;
  position: absolute;
  top: 6%;
  left: calc(50% + 4px);
}
@media (max-width: 1199px) {
  .contacts__wrapper--rightlined::after {
    left: calc(50% - 15px);
    height: 90%;
  }
}
@media (max-width: 767px) {
  .contacts__wrapper--rightlined::after {
    content: none;
  }
}
.contacts__item {
  margin: 0;
  color: #102654;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 152%;
  display: flex;
  flex-direction: column;
  gap: 4.5px;
}
@media (max-width: 1199px) {
  .contacts__item {
    font-size: 18px;
  }
}
.contacts__item span {
  color: #102654;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: 150%;
}
@media (max-width: 1199px) {
  .contacts__item span {
    font-size: 20px;
  }
}

